import IkaMapElements from "."
import { deregisterBind, registerBind } from "../../core/bind"

export function setBind(this: IkaMapElements, is: string, deregister?: boolean) {
    const param = { requester: this as any, bind: `ika-me:${is}`, global: this.hasAttribute('global') }
    deregister
        ? deregisterBind(param)
        : registerBind(param)
}